// 
const WAN = 10000;
const toWan = (yen) =>
  isFinite(yen) ? Math.round(((yen || 0) / WAN) * 10) / 10 : 0;

// 
// - 
// - 
const SH_FIXED_ANNUAL = {
  // [~75.6, 75.7~87.6, 87.6~99.6]
  s1: {
    u40: [131354, 137342, 143330],
    o40: [136922, 143870, 150818],
  },
  s2: {
    u40: [131111, 137057, 143003],
    o40: [136644, 143544, 150444],
  },
  s3: {
    u40: [132152, 137750, 143798],
    o40: [137339, 144360, 151380],
  },
};
const SH_RATE = {
  s1: { u40: 0.1444, o40: 0.1604 },
  s2: { u40: 0.1441, o40: 0.16 },
  s3: { u40: 0.1438, o40: 0.16 },
};

const getSocialEmpYen = (annualIncomeYen, scenarioKey, age) => {
  const income = Math.max(0, Number(annualIncomeYen) || 0);
  if (income <= 0) return 0;
  const ageKey = age >= 40 ? "o40" : "u40";

  const fixed = SH_FIXED_ANNUAL?.[scenarioKey]?.[ageKey];
  const rate = SH_RATE?.[scenarioKey]?.[ageKey];

  if (!Array.isArray(fixed) || fixed.length < 3 || !Number.isFinite(rate)) return 0;

  if (income <= 756000) return fixed[0];
  if (income <= 876000) return fixed[1];
  if (income < 1000000) return fixed[2];
  return income * rate;
};

// 
const taxBands = [
  { min: 0, max: 1950000, rate: 0.05, ded: 0 },
  { min: 1950000, max: 3300000, rate: 0.10, ded: 97500 },
  { min: 3300000, max: 6950000, rate: 0.20, ded: 427500 },
  { min: 6950000, max: 9000000, rate: 0.23, ded: 636000 },
  { min: 9000000, max: 18000000, rate: 0.33, ded: 1536000 },
  { min: 18000000, max: 40000000, rate: 0.40, ded: 2796000 },
  { min: 40000000, max: Infinity, rate: 0.45, ded: 4796000 },
];
const calcIncomeTax = (taxable) => {
  const t = Math.max(0, taxable);
  const b = taxBands.find((v) => t >= v.min && t < v.max) || taxBands[taxBands.length - 1];
  return Math.max(0, t * b.rate - b.ded);
};

// : 
const basicLT = (agi) => {
  if (agi <= 24000000) return 430000;
  if (agi <= 24500000) return 290000;
  if (agi <= 25000000) return 150000;
  return 0;
};

//  S1: 4832160S2: 95/88/68/63/58/48/32/16/0S3: 104/67/62/48/32/16/0
const basicIT = {
  s1: (agi) => {
    if (agi <= 24000000) return 480000;
    if (agi <= 24500000) return 320000;
    if (agi <= 25000000) return 160000;
    return 0;
  },
  s2: (agi) => {
    if (agi <= 1320000) return 950000;
    if (agi <= 3360000) return 880000;
    if (agi <= 4890000) return 680000;
    if (agi <= 6550000) return 630000;
    if (agi <= 23500000) return 580000;
    if (agi <= 24000000) return 480000;
    if (agi <= 24500000) return 320000;
    if (agi <= 25000000) return 160000;
    return 0;
  },
  s3: (agi) => {
    if (agi <= 4890000) return 1040000;
    if (agi <= 6550000) return 670000;
    if (agi <= 23500000) return 620000;
    if (agi <= 24000000) return 480000;
    if (agi <= 24500000) return 320000;
    if (agi <= 25000000) return 160000;
    return 0;
  },
};

// 
const empDed = {
  s1: (y) => {
    y = Math.max(0, y);
    if (y === 0) return 0;
    const cap = 1950000;
    if (y <= 1625000) return 550000;
    if (y <= 1800000) return Math.min(cap, y * 0.4 - 100000);
    if (y <= 3600000) return Math.min(cap, y * 0.3 + 80000);
    if (y <= 6600000) return Math.min(cap, y * 0.2 + 440000);
    if (y <= 8500000) return Math.min(cap, y * 0.1 + 1100000);
    return cap;
  },
  s2: (y) => {
    y = Math.max(0, y);
    if (y === 0) return 0;
    const cap = 1950000;
    if (y <= 1900000) return 650000;
    if (y <= 3600000) return Math.min(cap, y * 0.3 + 80000);
    if (y <= 6600000) return Math.min(cap, y * 0.2 + 440000);
    if (y <= 8500000) return Math.min(cap, y * 0.1 + 1100000);
    return cap;
  },
  s3: (y) => {
    y = Math.max(0, y);
    if (y === 0) return 0;
    const cap = 1950000;
    if (y <= 2200000) return 740000;
    if (y <= 3600000) return Math.min(cap, y * 0.3 + 80000);
    if (y <= 6600000) return Math.min(cap, y * 0.2 + 440000);
    if (y <= 8500000) return Math.min(cap, y * 0.1 + 1100000);
    return cap;
  },
};

