  const netRange = useMemo(() => {
    let min = Infinity;
    let max = -Infinity;
    data.forEach((row) => {
      scenarios.forEach((s) => {
        const v = Number(row[`net_${s.key}`]);
        if (!Number.isFinite(v)) return;
        min = Math.min(min, v);
        max = Math.max(max, v);
      });
    });
    if (min === Infinity || max === -Infinity) return { min: 0, max: 0 };
    return { min, max };
  }, [data]);

  const netDomain = useMemo(() => {
    // 50 min 
    const step = 50;
    const lo = Math.min(0, Number(netRange.min) || 0);
    const hi = Math.max(0, Number(netRange.max) || 0);
    const min = Math.floor(lo / step) * step;
    const max = Math.ceil(hi / step) * step;
    return [min, max];
  }, [netRange]);

  // 
  const breakdownFixed = useMemo(() => {
    if (!staticReady) return null;
    if (!displayRowFixed) return null;
    if (!fixedTaxMarks) return null;

    const out = {};
    const salaryWan = Number(displaySalary) || 0;
    const otherWan = Number(otherIncome) || 0;
    const ageVal = Number(age) || 0;

    // 
    const spouseDedWan = Number(spouseDedInput) || 0;
    const spouseSpecWan = Number(spouseSpecInput) || 0;
    const widowWan = Number(deductions.widow) || 0;
    const singleParentWan = Number(deductions.singleParent) || 0;
    const workingStudentWan = Number(deductions.workingStudent) || 0;
    const otherDedWan = Number(deductions.otherDed) || 0;

    scenarios.forEach((s) => {
      out[`age_${s.key}`] = ageVal;
      out[`salary_${s.key}`] = salaryWan;

      const empWan = Number(displayRowFixed[`emp_${s.key}`]) || 0;
      const incomeB = Math.max(0, salaryWan - empWan);
      const incomeD = incomeB + otherWan;

      out[`emp_${s.key}`] = empWan;
      out[`incomeB_${s.key}`] = incomeB;
      out[`otherC_${s.key}`] = otherWan;
      out[`incomeD_${s.key}`] = incomeD;

      // /
      const basicITWan = Number(displayRowFixed[`basicIT_${s.key}`]) || 0;
      const basicLTWan = Number(displayRowFixed[`basicLT_${s.key}`]) || 0;
      out[`basicIT_${s.key}`] = basicITWan;
      out[`basicLT_${s.key}`] = basicLTWan;

      out[`spouseDed_${s.key}`] = spouseDedWan;
      out[`spouseSpec_${s.key}`] = spouseSpecWan;
      out[`widow_${s.key}`] = widowWan;
      out[`singleParent_${s.key}`] = singleParentWan;
      out[`workingStudent_${s.key}`] = workingStudentWan;
      out[`otherDed_${s.key}`] = otherDedWan;

      // /
      const dedBaseCommon =
        spouseDedWan +
        spouseSpecWan +
        widowWan +
        singleParentWan +
        workingStudentWan +
        otherDedWan;
      out[`dedIT_${s.key}`] = dedBaseCommon + basicITWan;
      out[`dedLT_${s.key}`] = dedBaseCommon + basicLTWan;

      // 
      const taxableITWan = fixedTaxMarks.it[s.key]?.taxable ?? 0;
      const taxableLTWan = fixedTaxMarks.lt[s.key]?.taxable ?? 0;
      out[`taxableIT_${s.key}`] = taxableITWan;
      out[`taxableLT_${s.key}`] = taxableLTWan;

      // 
      const socialWan = Number(displayRowFixed[`social_${s.key}`]) || 0;
      const taxITWan = fixedTaxMarks.it[s.key]?.tax ?? 0;
      const taxLTWan = fixedTaxMarks.lt[s.key]?.tax ?? 0;
      const payTotal = socialWan + taxITWan + taxLTWan;

      out[`social_${s.key}`] = socialWan;
      out[`taxIT_${s.key}`] = taxITWan;
      out[`taxLT_${s.key}`] = taxLTWan;
      out[`payTotal_${s.key}`] = payTotal;
      // (A) + (C) - (+)
      out[`net_${s.key}`] = salaryWan + otherWan - payTotal;
    });

    return out;
  }, [
    staticReady,
    displayRowFixed,
    fixedTaxMarks,
    displaySalary,
    age,
    otherIncome,
    spouseDedInput,
    spouseSpecInput,
    deductions,
  ]);

  // 
  const breakdownDynamic = useMemo(() => {
    const out = {};
    const salaryWan = Number(displaySalary) || 0;
    const otherWan = Number(otherIncome) || 0;
    const ageVal = Number(age) || 0;

    const spouseDedWan = Number(spouseDedInput) || 0;
    const spouseSpecWan = Number(spouseSpecInput) || 0;
    const widowWan = Number(deductions.widow) || 0;
    const singleParentWan = Number(deductions.singleParent) || 0;
    const workingStudentWan = Number(deductions.workingStudent) || 0;
    const otherDedWan = Number(deductions.otherDed) || 0;

    const dedBaseCommonWan =
      spouseDedWan +
      spouseSpecWan +
      widowWan +
      singleParentWan +
      workingStudentWan +
      otherDedWan;

    scenarios.forEach((s) => {
      out[`age_${s.key}`] = ageVal;
      out[`salary_${s.key}`] = salaryWan;

      const salaryYen = salaryWan * WAN;
      const empYen = empDed[s.key]?.(salaryYen) || 0;
      const empWan = toWan(empYen);

      const incomeB = Math.max(0, salaryWan - empWan);
      const incomeD = incomeB + otherWan;

      // 
      const agiYen = Math.max(0, incomeD) * WAN;
      const basicITYen = basicIT[s.key]?.(agiYen) || 0;
      const basicLTYen = basicLT(agiYen) || 0;
      const basicITWan = toWan(basicITYen);
      const basicLTWan = toWan(basicLTYen);

      const socialYen = getSocialEmpYen(salaryYen, s.key, ageVal);
      const socialWan = toWan(socialYen);

      const dedITWan = dedBaseCommonWan + basicITWan;
      const dedLTWan = dedBaseCommonWan + basicLTWan;

      const taxableITWan = Math.max(0, incomeD - socialWan - dedITWan);
      const taxableLTWan = Math.max(0, incomeD - socialWan - dedLTWan);
      const taxITYen = calcIncomeTax(taxableITWan * WAN);
      const taxLTYen = taxableLTWan * WAN * 0.1;
      const taxITWan = toWan(taxITYen);
      const taxLTWan = toWan(taxLTYen);
      const payTotal = socialWan + taxITWan + taxLTWan;
      const net = salaryWan + otherWan - payTotal;

      out[`emp_${s.key}`] = empWan;
      out[`incomeB_${s.key}`] = incomeB;
      out[`otherC_${s.key}`] = otherWan;
      out[`incomeD_${s.key}`] = incomeD;

      out[`basicIT_${s.key}`] = basicITWan;
      out[`basicLT_${s.key}`] = basicLTWan;
      out[`spouseDed_${s.key}`] = spouseDedWan;
      out[`spouseSpec_${s.key}`] = spouseSpecWan;
      out[`widow_${s.key}`] = widowWan;
      out[`singleParent_${s.key}`] = singleParentWan;
      out[`workingStudent_${s.key}`] = workingStudentWan;
      out[`otherDed_${s.key}`] = otherDedWan;
      out[`dedIT_${s.key}`] = dedITWan;
      out[`dedLT_${s.key}`] = dedLTWan;

      out[`taxableIT_${s.key}`] = taxableITWan;
      out[`taxableLT_${s.key}`] = taxableLTWan;

      out[`social_${s.key}`] = socialWan;
      out[`taxIT_${s.key}`] = taxITWan;
      out[`taxLT_${s.key}`] = taxLTWan;
      out[`payTotal_${s.key}`] = payTotal;
      out[`net_${s.key}`] = net;
    });

    return out;
  }, [displaySalary, otherIncome, age, spouseDedInput, spouseSpecInput, deductions]);

  // : (+)  100 %
  const ratioBarData = useMemo(() => {
    const gross = (Number(displaySalary) || 0) + (Number(otherIncome) || 0); // 
    if (gross === 0) {
      return scenarios.map((s) => ({
        scenario: s.label,
        netPct: 0,
        socialPct: 0,
        taxITPct: 0,
        taxLTPct: 0,
      }));
    }

    return scenarios.map((s) => {
      const social = Number(breakdownDynamic?.[`social_${s.key}`]) || 0;
      const taxIT = Number(breakdownDynamic?.[`taxIT_${s.key}`]) || 0;
      const taxLT = Number(breakdownDynamic?.[`taxLT_${s.key}`]) || 0;

      //  100 net 1
      const socialPct = round1((social / gross) * 100);
      const taxITPct = round1((taxIT / gross) * 100);
      const taxLTPct = round1((taxLT / gross) * 100);
      const netPct = round1(100 - (socialPct + taxITPct + taxLTPct));

      return {
        scenario: s.label,
        netPct,
        socialPct,
        taxITPct,
        taxLTPct,
      };
    });
  }, [breakdownDynamic, displaySalary, otherIncome]);

